<template>
    <div>
        <div class="title">
            <h3 class="yun-h3"> popup</h3>
            <p>YunUI </p>
        </div>
        <div class="content button-container">
            <yun-button type="linear" @click.native="showPopup1 = !showPopup1">
            </yun-button>
            <yun-button type="linear" @click.native="showPopup2 = !showPopup2"></yun-button>
            <yun-button type="linear" @click.native="showPopup3 = !showPopup3"></yun-button>
            <yun-button type="linear" @click.native="showPopup4 = !showPopup4"></yun-button>
            <yun-button type="linear" @click.native="showPopup5 = !showPopup5"></yun-button>
            <yun-button type="linear" @click.native="showPopup6 = !showPopup6"></yun-button>
        </div>

        <yun-popup title="" v-if="showPopup1" @maskClicked="maskClicked"
                   content="
                            ">
            <yun-button-group slot="buttonGroup">
                <yun-tab-item title="" class="active"></yun-tab-item>
            </yun-button-group>
        </yun-popup>

        <yun-popup title="" v-if="showPopup2" @maskClicked="maskClicked"
                   input="input">
            <yun-button-group slot="buttonGroup">
                <yun-tab-item title=""></yun-tab-item>
                <yun-tab-item class="active" title=""></yun-tab-item>
            </yun-button-group>
        </yun-popup>

        <yun-popup title="" v-if="showPopup3" @maskClicked="maskClicked"
                   input="textarea">
            <yun-button-group slot="buttonGroup">
                <yun-tab-item title=""></yun-tab-item>
                <yun-tab-item class="active" title=""></yun-tab-item>
            </yun-button-group>
        </yun-popup>

        <yun-popup title="" v-if="showPopup4" @maskClicked="maskClicked"
                   content="
                            ">
            <yun-button size="large" type="normal" slot="button"></yun-button>
            <yun-button size="large" type="normal" disabled slot="button"></yun-button>
            <yun-button-group slot="buttonGroup">
                <yun-tab-item title=""></yun-tab-item>
            </yun-button-group>
        </yun-popup>

        <yun-popup title="" v-if="showPopup5" @maskClicked="maskClicked"
                   :textList="textListArray">
            <img src="../assets/popup-cover.png" slot="cover"/>
            <yun-button-group slot="buttonGroup">
                <yun-tab-item title=""></yun-tab-item>
                <yun-tab-item title="" class="active"></yun-tab-item>
            </yun-button-group>
        </yun-popup>

        <yun-popup title="" v-if="showPopup6" input="input" @maskClicked="maskClicked"
                   content="
                            ">
            <yun-button-group slot="buttonGroup">
                <yun-tab-item title=""></yun-tab-item>
            </yun-button-group>
        </yun-popup>
    </div>
</template>

<script>
    export default {
        name: 'yun-toast-demo',
        props: {},
        data: function () {
            return {
                showPopup1: false,
                showPopup2: false,
                showPopup3: false,
                showPopup4: false,
                showPopup5: false,
                showPopup6: false,
                textListArray: [
                    {
                        content: ''
                    },
                    {
                        content: ''
                    },
                    {
                        content: ''
                    }
                ]
            }
        },
        created () {
            console.log('button demo loaded')
        },
        methods: {
            maskClicked: function () {
                this.showPopup1 = false
                this.showPopup2 = false
                this.showPopup3 = false
                this.showPopup4 = false
                this.showPopup5 = false
                this.showPopup6 = false
            }
        }
    }

</script>

<style lang="less" rel="stylesheet/less">
    .close-popup {
        position: fixed;
        z-index: 10;
        color: #ffffff;
        right: 12px;
        top: 12px;
        width: 44px;
        height: 44px;
        border-radius: 50%;
        line-height: 44px;
        text-align: center;
        background-color: rgba(255, 255, 255, 0.2);
        &:hover {
            background-color: rgba(255, 255, 255, 0.4);
            cursor: pointer;
        }
    }
</style>



