<template>
    <div>
        <div class="title">
            <h3 class="yun-h3"> Toast</h3>
            <p>YunUI  <strong></strong>
                 <strong></strong> </p>
        </div>
        <div class="content button-container">
            <yun-button type="linear" @click.native="showToast = !showToast"></yun-button>
            <yun-button type="linear" @click.native="showToastBottom = !showToastBottom"></yun-button>
        </div>

        <yun-toast content="" :show="showToast">
            <img src="../assets/success.png" slot="icon" />
        </yun-toast>
        <yun-toast content="" bottom :show="showToastBottom"></yun-toast>
    </div>
</template>

<script>
    export default {
        name: 'yun-toast-demo',
        props: {},
        data: function () {
            return {
                showToast: false,
                showToastBottom: false
            }
        },
        created () {
            console.log('button demo loaded')
        },
        methods: {}
    }

</script>

<style lang="less">

</style>



