            <yun-button type="linear" @click.native="goToPage('statePage6')"></yun-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'yun-statePage-demo',
        props: {},
        created () {
            console.log('button demo loaded')
        },
        methods: {
            goToPage: function (path) {
                this.$router.push({
                    path: '/statePage/' + path
