<template>
    <div>
        <div class="title">
            <h3 class="yun-h3"> Loading</h3>
            <p>YunUI 
                 <strong></strong> 
                 <strong></strong> </p>
        </div>
        <div class="content button-container">
            <yun-button type="linear" @click.native="showFresh = !showFresh"> Loading</yun-button>
            <yun-button type="linear" @click.native="showLoading = !showLoading"> Loading</yun-button>
        </div>

        <yun-loading content="" refresh :show="showFresh" style="top:64px">
            <img src="../assets/loading.png" slot="icon"/>
        </yun-loading>
        <yun-loading content="" :show="showLoading">
            <img src="../assets/loading.png" slot="icon"/>
        </yun-loading>
    </div>
</template>

<script>
    export default {
        name: 'yun-loading-demo',
        props: {},
        data: function () {
            return {
                showFresh: false,
                showLoading: false
            }
        },
        created () {
            console.log('loading demo loaded')
        },
        methods: {}
    }

</script>

<style lang="less">
    .is-loading-refresh {
        top: 64px;
    }
</style>



